class SubDisplay extends Component{
    constructor(props){
        super(props);
        this.state = {
            screen_title : this.props.screen_title,
            screen_display_list : []
        };

        this.Map_screen_display = this.Map_screen_display.bind(this);

    };

    Map_screen_display(e){
        let self = this;
        if (self.props.screen_data !== undefined){
            self.setState({
                screen_display_list: self.props.screen_data
            });
        }
    }

    componentWillMount(e){
        this.Map_screen_display(e);
    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <small> <i className={'fa fa-users'}> </i> {(this.state.screen_title) ? this.state.screen_title : "Sub Display"} </small> </strong> </h3>
                </div>

                <table className={'table table-responsive'}>
                    <thead>
                        <tr>
                            <td> <strong> Names </strong></td>
                            <td> <strong> Surname </strong></td>
                            <td> <strong> ID Number </strong></td>
                            <td> <strong> Date of Birth </strong></td>
                            <td> <strong> Relationship </strong></td>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.screen_display_list.map( screen => {
                                return(
                                    <tr>
                                        <td> <strong> <small> <em> {screen.full_names} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.surname} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.id_number} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.date_of_birth} </em></small></strong></td>
                                        <td> <strong> <small> <em> {screen.relationship} </em></small></strong></td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td> <strong> Names </strong></td>
                            <td> <strong> Surname </strong></td>
                            <td> <strong> ID Number </strong></td>
                            <td> <strong> Date of Birth </strong></td>
                            <td> <strong> Relationship </strong></td>
                        </tr>
                    </tfoot>
                </table>
            </div>
        )
    }
}

class PolicyHolder extends Component{
    constructor(props){
        super(props);
        this.state = {
            policy_holder : {...policy_holder_details},

            show_personal_details : true,
            show_postal_address : false,
            show_physical_address : false,
            show_contact_details : false,
            show_banking_details : false,


            form_response : "",
        };

        this.onChangeHandler = this.onChangeHandler.bind(this);
        this.onShow = this.onShow.bind(this);

    };

    onChangeHandler(e){
        let policy_holider = Object.assign({},this.state.policy_holder);
        switch (e.target.name) {
            case 'title' : policy_holider.title = e.target.value;break;
            case 'surname' : policy_holider.surname = e.target.value;break;
            case 'full_names' : policy_holider.full_names = e.target.value;break;
            case 'id_number' : policy_holider.id_number = e.target.value;break;
            case 'date_of_birth' : policy_holider.date_of_birth = e.target.value;break;
            case 'nationality' : policy_holider.nationality = e.target.value;break;

            default: break;
        };

        this.setState({
            policy_holder: policy_holider
        });

    }

    onShow(e){
        
        switch (e.target.name) {
            case 'personal-details' :
                this.setState({
                    show_personal_details : true,
                    show_postal_address : false,
                    show_physical_address : false,
                    show_contact_details : false,
                    show_banking_details : false

                });break;

            case 'physical-address':
                this.setState({
                    show_personal_details : false,
                    show_postal_address : false,
                    show_physical_address : true,
                    show_contact_details : false,
                    show_banking_details : false

                });break;

            case 'postal-address':
                this.setState({
                    show_personal_details : false,
                    show_postal_address : true,
                    show_physical_address : false,
                    show_contact_details : false,
                    show_banking_details : false

                });break;
            case 'contact-details':
                this.setState({
                    show_personal_details : false,
                    show_postal_address : false,
                    show_physical_address : false,
                    show_contact_details : true,
                    show_banking_details : false
                });break;

            case 'banking-details':
                this.setState({
                    show_personal_details : false,
                    show_postal_address : false,
                    show_physical_address : false,
                    show_contact_details : false,
                    show_banking_details : true

                })


        }
    }



    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className={'fa fa-user'}> </i> Policy Holder </strong></h3>

                    <div className={'row pull-right'}>
                        <button className='btn btn-box-tool btn-outline-dark' name={'personal-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-user'> </i> Personal Details </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'physical-address'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-map-o'> </i> Physical Address </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'postal-address'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-envelope'> </i> Postal Address </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'contact-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-mobile'> </i> Contact Details </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'banking-details'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-bank'> </i> Banking Details </strong></button>
                    </div>
                </div>

                <div className={'row'}>
                    <div className={'col-md-6'}>
                        <div className={'box box-footer'}>
                            <div className={'box box-header'}>
                                <h3 className={'box-title'}> <strong> <i className={'fa fa-user'}> </i> Personal Details </strong></h3>
                            </div>
                            <form className={'form-horizontal'}>

                                <div className={'form-group'}>
                                    <label>Title</label>
                                    <div className={'input-group'}>
                                        <select className={'form-control'} name={'title'} value={this.state.policy_holder.title} onChange={e => this.onChangeHandler(e)} >
                                            <option value={'mr'}> MR </option>
                                            <option value={'mrs'}> Mrs </option>
                                            <option value={'miss'}> Miss </option>
                                        </select>
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Surname</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'surname'} value={this.state.policy_holder.surname} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Full Names</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'full_names'} value={this.state.policy_holder.full_names} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>ID Number</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'id_number'} value={this.state.policy_holder.id_number} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>DOB</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'date_of_birth'} value={this.state.policy_holder.date_of_birth} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <label>Nationality</label>
                                    <div className={'input-group'}>
                                        <input type={'text'} className={'form-control'} name={'nationality'} value={this.state.policy_holder.nationality} onChange={e => this.onChangeHandler(e)} />
                                    </div>
                                </div>

                                <div className={'form-group'}>
                                    <button type={'button'} className={'btn btn-success btn-block'}><strong> <i className={'fa fa-save'}> </i> Save Policy </strong></button>
                                </div>


                            </form>

                        </div>
                    </div>
                    <div className={'col-md-6'}>
                        {
                            (this.state.show_postal_address) ?
                                <Addresses.Postal />
                            :""
                        }
                        {
                            (this.state.show_physical_address)?
                                <Addresses.Physical />
                            :""
                        }
                        {
                            (this.state.show_contact_details) ?
                                <Contacts.ContactDetails />
                            :""
                        }
                        {
                            (this.state.show_banking_details) ?
                                <BankingDetails />
                            :""
                        }
                    </div>
                </div>

            </div>
        )
    }
}


class CoverAdmin extends Component{
    constructor(props){
        super(props);
        this.state = {
            admin : "",
        };
    };


    render(){
        return(
            <div className='box box-footer'>
                <div className='box box-header'>
                    <h3 className='box-title'> <strong>  <i className='fa fa-dashboard'> </i> Admin </strong> </h3>
                    <div className='row pull-right'>
                            <button className='btn btn-box-tool btn-outline-dark'><strong> <i className='fa fa-money'> </i> Payments Details </strong></button>
                            <button className='btn btn-box-tool btn-outline-dark'><strong> <i className='fa fa-cube'> </i> Create Cover </strong></button>
                            

                    </div>

                </div>


            
            </div>
        )
    }
}


class Covers extends Component{
    constructor(){
        super();
        this.state ={

            show_policy_holder : false,
            show_spouse : false,
            show_children : false,
            show_extended : false,
            show_beneficiary : false,
            show_admin : false,
        };

        this.onShow = this.onShow.bind(this);
    };

    onShow(e){

        switch(e.target.name){
            case 'policy-holder':
                this.setState({
                    show_policy_holder : true,
                    show_spouse : false,
                    show_children : false,
                    show_extended : false,
                    show_beneficiary : false,
                    show_admin : false,

                });break;

            case 'spouse':
                this.setState({
                    show_policy_holder : false,
                    show_spouse : true,
                    show_children : false,
                    show_extended : false,
                    show_beneficiary : false,
                    show_admin : false,

                });break;

            case 'children':
                this.setState({
                    show_policy_holder : false,
                    show_spouse : false,
                    show_children : true,
                    show_extended : false,
                    show_beneficiary : false,
                    show_admin : false,

                });break;

            case 'extended':
                this.setState({
                    show_policy_holder : false,
                    show_spouse : false,
                    show_children : false,
                    show_extended : true,
                    show_beneficiary : false,
                    show_admin : false,

                });break;

            case 'beneficiary':
                this.setState({
                    show_policy_holder : false,
                    show_spouse : false,
                    show_children : false,
                    show_extended : false,
                    show_beneficiary : true,
                    show_admin : false,
                });break;

            case 'admin':
                this.setState({
                    show_policy_holder : false,
                    show_spouse : false,
                    show_children : false,
                    show_extended : false,
                    show_beneficiary : false,
                    show_admin : true,
                });break;

            default:break;



        }


    }

    render(){
        return(
            <div className={'box box-footer'}>
                <div className={'box box-header'}>
                    <h3 className={'box-title'}> <strong> <i className='fa fa-users'> </i> Covers</strong></h3>

                    <div className={'row pull-right'}>
                        <button className='btn btn-box-tool btn-outline-dark' name={'policy-holder'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Policy Holder </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'spouse'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Spouse </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'children'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Children </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark' name={'extended'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Extended </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark'  name={'beneficiary'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Beneficiary </strong></button>
                        <button className='btn btn-box-tool btn-outline-dark'  name={'admin'} onClick={e => this.onShow(e)}><strong> <i className='fa fa-users'> </i> Administration </strong></button>

                    </div>
                </div>


                {
                    (this.state.show_policy_holder) ?
                        <PolicyHolder/>
                    :""
                }

                {
                    (this.state.show_spouse) ?
                        <Spouse />
                    :""
                }

                {
                    (this.state.show_children) ?
                        <Child/>
                    :''
                }
                {
                    (this.state.show_extended)?
                        <ExtendedFamily/>
                    :""
                }
                {
                    (this.state.show_beneficiary) ?
                        <Beneficiary />
                    :""
                }

                {
                    (this.state.show_admin) ?
                    <CoverAdmin />
                    :""
                }

            </div>
        )
    }


}



export default Covers;